body {
    background-color: #f0e8e8;
    font-family: sans-serif;
}

.container {
    background-color: #e0dada;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 5px 5px 10px #c7c1c1,
        -5px -5px 10px #f6f2f2;
    width: 400px;
    margin-top: 10%;
    margin-left: 30%;
}

h1 {
    font-size: 36px;
    margin-top: 0;
    color: rgb(142, 113, 77);
}

p {
    font-size: 18px;
    margin-bottom: 10px;
}

input[type=number] {
    padding: 10px;
    font-size: 20px;
    border: none;
    background-color: #c7c1c1;
    border-radius: 5px;
    color: #fff;
    margin-right: 10px;
    box-shadow: 3px 3px 5px #c7c1c1,
        -3px -3px 5px #f6f2f2;
    width: 70%;
}

button {
    padding: 10px;
    font-size: 18px;
    border: none;
    border-radius: 5px;
    margin-right: 10px;
    box-shadow: 3px 3px 5px #c7c1c1,
        -3px -3px 5px #f6f2f2;
    cursor: pointer;
}

#guess-btn {
    background-color: #66b9f0;
    color: #fff;
}

#reset-btn {
    background-color: #ff6b6b;
    color: #fff;
}

#message {
    margin-top: 10px;
    font-size: 18px;
    font-weight: bold;
}